.react-toggle {
  touch-action: pan-x;

  display: inline-block;
  position: relative;
  cursor: pointer;
  background-color: transparent;
  border: 0;
  padding: 0;

  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;

  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
  -webkit-tap-highlight-color: transparent;
}

.react-toggle-screenreader-only {
  border: 0;
  clip: rect(0 0 0 0);
  height: 1px;
  margin: -1px;
  overflow: hidden;
  padding: 0;
  position: absolute;
  width: 1px;
}

//

.react-toggle--disabled {
  cursor: not-allowed;
  opacity: 0.5;
  -webkit-transition: opacity 0.25s;
  transition: opacity 0.25s;

}

.react-toggle-track {
  width: 50px;
  height: 24px;
  padding: 0;
  border-radius: 30px;
  background: linear-gradient(#378FE6, #3EDA82);

}

.react-toggle:hover:not(.react-toggle--disabled) .react-toggle-track {
  background: linear-gradient(#4698eb, #4ed388);
}

.react-toggle--checked .react-toggle-track {
  background: #aeb3cb;
  transition: all 0.3s;
}

.react-toggle--checked:hover:not(.react-toggle--disabled) .react-toggle-track {
  background: linear-gradient(#378FE6, #3EDA82);
}

.react-toggle-track-check {
  position: absolute;
  width: 14px;
  height: 10px;
  top: 0px;
  bottom: 0px;
  margin-top: auto;
  margin-bottom: auto;
  line-height: 0;
  left: 8px;
  opacity: 0;
  -webkit-transition: opacity 0.25s ease;
  -moz-transition: opacity 0.25s ease;
  transition: opacity 0.25s ease;
}

.react-toggle--checked .react-toggle-track-check {
  opacity: 1;
  -webkit-transition: opacity 0.25s ease;
  -moz-transition: opacity 0.25s ease;
  transition: opacity 0.25s ease;
}

.react-toggle-track-x {
  position: absolute;
  width: 10px;
  height: 10px;
  top: 0px;
  bottom: 0px;
  margin-top: auto;
  margin-bottom: auto;
  line-height: 0;
  right: 10px;
  opacity: 1;
  -webkit-transition: opacity 0.25s ease;
  -moz-transition: opacity 0.25s ease;
  transition: opacity 0.25s ease;
}

.react-toggle--checked .react-toggle-track-x {
  opacity: 0;
}

.react-toggle-thumb {
  transition: all 0.5s cubic-bezier(0.23, 1, 0.32, 1) 0ms;
  position: absolute;
  top: 2px;
  left: 2px;
  width: 20px;
  height: 20px;
  // border: 3px solid linear-gradient(#378FE6, #3EDA82);
  border-radius: 50%;
  background-color: hsl(230, 17%, 14%);

  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;

  -webkit-transition: all 0.25s ease;
  -moz-transition: all 0.25s ease;
  transition: all 0.25s ease;
}

.react-toggle:hover:not(.react-toggle--disabled) .react-toggle-thumb {
  background-color: hsl(229, 16%, 21%);
  transition: all 0.3s;

}

.react-toggle--checked .react-toggle-thumb {
  left: 27px;
  background-color: hsl(0, 0%, 100%);
  transition: all 0.3s;

}

.react-toggle--checked:hover:not(.react-toggle--disabled) .react-toggle-thumb {
  transition: all 0.3s;
  background-color: hsl(0, 0%, 100%);
}

